___CSS_LOADER_EXPORT___.push([module.id, `button, a, .card {
  min-width: 44px; /* Minimum width */
  min-height: 44px; /* Minimum height */
  padding: 10px; /* Adding padding to ensure sufficient touch area */
  display: inline-flex; /* To align content inside */
  justify-content: center; /* Align horizontally */
  align-items: center; /* Align vertically */
}

button {
  font-size: 24px; /* To make the button easily tappable */
  line-height: 44px; /* Ensures the button content is vertically centered */
}

a {
  font-size: 16px; /* Readable font size for links */
  text-decoration: none;
  color: inherit;
}


h2 {
   text-align: center;
   margin: 40px;
}

body {
  margin: 0;
  font-family: Arial, sans-serif;
}

header {
  background-color: #333;
  color: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 20px;
}

nav {
  display: flex;
  justify-content: flex-end; /* Pindahkan item ke kanan */
  align-items: center;
  width: 100%; /* Mengambil seluruh lebar container */
}

#menu-button {
  display: none;
  background: none;
  border: none;
  color: white;
  font-size: 1.5rem;
  cursor: pointer;
}

#brand {
  font-size: 1.2rem;
  position: absolute; /* Membuat posisi relatif terhadap parent */
  top: 5; /* Atur jarak dari atas */
  left: 0; /* Atur jarak dari kiri */
  padding: 10px;
  margin-left: 13px;
}

#nav-links {
  list-style: none;
  display: flex;
  gap: 15px;
  margin: 0;
  padding: 0;
}

#nav-links li {
  cursor: pointer;
}

nav ul li a {
  text-decoration: none;
  color: white;
  font-size: 1rem;
  transition: color 0.3s;
}

nav ul li a:hover {
  color: #007bff;
}

.hero-element img {
  width: 100%;
  height: 300px;
  object-fit: cover;
}

.explore-section {
  padding: 20px;
}

.explore-section h2 {
  text-align: center;
}

.restaurant-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 20px;
  /* Jarak antar kartu */
  justify-content: center;
  /* Rata tengah secara horizontal */
  align-items: start;
  /* Semua item berada di atas */
  max-width: 1200px;
  /* Lebar maksimum grid */
  margin: 0 auto;
  /* Tengah secara horizontal pada halaman */
}

restaurant-card {
  display: block;
  border: 1px solid #ddd;
  border-radius: 8px;
  padding: 10px;
  text-align: center;
}

footer {
  text-align: center;
  background-color: #333;
  color: white;
  padding: 10px 0;
}

.hero-element img {
  width: 100%;
  height: 300px;
  object-fit: cover;
}

/* Menyembunyikan link skip secara default */
.skip-link {
  position: absolute;
  top: -70px; /* Menyembunyikan link di atas layar */
  left: 10px;
  background-color: #000;
  color: white;
  padding: 10px;
  text-decoration: none;
  border-radius: 5px;
}

/* Menampilkan link saat fokus dengan keyboard */
.skip-link:focus {
  top: 10px; /* Pindahkan link ke atas saat fokus */
  z-index: 1000; /* Pastikan berada di atas elemen lain */
}


@media (max-width: 1200px) {
  .hero-element img {
    width: 100%; /* Membuat gambar full width */
    height: auto; /* Menyesuaikan tinggi gambar agar proporsional */
  }
}

@media (max-width: 600px) {
  #menu-button {
    display: inline-block;
  }

  #brand {
  margin-left: 10px;
}



  #nav-links {
    display: none;
    flex-direction: column;
    background-color: #444;
    position: absolute;
    top: 50px;
    right: 10px;
    padding: 10px;
    border-radius: 5px;
  }

  #nav-links.show {
    display: flex;
  }

  header {
    padding: 10px 20px;
  }

  nav ul li a:hover {
    color: #007bff;
  }
}
